import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import { Link } from 'react-router-dom';
import GoldPageChart from './GoldPageChart';
import GoldPageBuy from './GoldPageBuy';
import GoldPageSell from './GoldPageSell';
import GoldPageSearch from './GoldPageSearch';
import GoldCalculator from './GoldCalculator';
import './GoldPage.scss';

@inject('gold')
@observer
class GoldPage extends Component {
  delayPageMove = (pageName, e) => {
    e.preventDefault();
    const {
      history: { push }
    } = this.props;
    const { common } = this.props.gold.root;
    common.loadingDelay(push, pageName);
  };

  handleStartDay = value => {
    const { gold } = this.props;
    gold.actStartDay(value);
  };
  handleEndDay = value => {
    const { gold } = this.props;
    gold.actEndDay(value);
  };
  handleDaySearch = value => {
    const { gold } = this.props;
    gold.actDaySearch();
  };

  render() {
    const {
      delayPageMove,
      handleStartDay,
      handleEndDay,
      handleDaySearch
    } = this;
    const { gold } = this.props;

    const buyList = gold.goldBuy.map((info, index) => {
      return (
        <GoldPageBuy
          key={index}
          date={info.date}
          g24k_b={info.g24k_b}
          g24k_b_ch={info.g24k_b_ch}
        />
      );
    });

    const sellList = gold.goldSell.map((info, index) => {
      return (
        <GoldPageSell
          key={index}
          date={info.date}
          g24k_s={info.g24k_s}
          g18k_s={info.g18k_s}
          g14k_s={info.g14k_s}
          g24k_s_ch={info.g24k_s_ch}
          g18k_s_ch={info.g18k_s_ch}
          g14k_s_ch={info.g14k_s_ch}
        />
      );
    });

    return (
      <div className="GoldPage">
        <h2> </h2>

        <div className="in_select clearfix">
          <GoldPageSearch
            onStartDay={handleStartDay}
            onEndDay={handleEndDay}
            onDaySearch={handleDaySearch}
          />
          <GoldCalculator />
        </div>

        <div className="sise">
          <div className="sise_left">
            <p className="st"> </p>
            <p className="sb">
              {String(gold.todayBuySise).replace(
                /(\d)(?=(?:\d{3})+(?!\d))/g,
                '$1,'
              )}
              <span></span>
            </p>
          </div>
          <div className="sise_right">
            <p className="st"> </p>
            <p className="sb">
              {String(gold.todaySellSise).replace(
                /(\d)(?=(?:\d{3})+(?!\d))/g,
                '$1,'
              )}
              <span></span>
            </p>
          </div>
        </div>
        <GoldPageChart
          onDate={gold.goldChart.date}
          onBuy={gold.goldChart.g24k_b}
          onSell={gold.goldChart.g24k_s}
        />

        <p className="right">: , 3.75g(1)</p>

        <div className="glist">
          <div className="gleft">
            <h4></h4>
            <ul>{buyList}</ul>
          </div>
          <div className="gright">
            <h4></h4>
            <ul>{sellList}</ul>
          </div>
        </div>

        <div className="home_btn">
          <p>
            :{' '}
            <a
              href="http://www.koreagoldx.co.kr/"
              target="_blank"
              rel="noopener noreferrer"
            >
              
            </a>
          </p>
          <Link
            to={{
              pathname: ''
            }}
            onClick={e => delayPageMove('', e)}
          >
            
          </Link>
        </div>
      </div>
    );
  }
}

export default GoldPage;
