import React from 'react';

const GoldPageSell = ({
  date,
  g24k_s,
  g18k_s,
  g14k_s,
  g24k_s_ch,
  g18k_s_ch,
  g14k_s_ch
}) => {
  return (
    <li>
      <div className="date">
        {date.substring(0, 4)}
        <br />
        {date.substring(5, 10)}
      </div>
      <div className="sub">
        <p>
          {' '}
          <span className="large">
            {String(g24k_s).replace(/(\d)(?=(?:\d{3})+(?!\d))/g, '$1,')}
          </span>
          <br />
          <em className="color_gray">
            (
            {g24k_s_ch > 0 ? (
              <span className="red"></span>
            ) : g24k_s_ch < 0 ? (
              <span className="blue"></span>
            ) : (
              <span className="etc">-</span>
            )}{' '}
            {String(g24k_s_ch).replace(/(\d)(?=(?:\d{3})+(?!\d))/g, '$1,')})
          </em>
        </p>
        <p>
          18K{' '}
          <span className="large">
            {String(g18k_s).replace(/(\d)(?=(?:\d{3})+(?!\d))/g, '$1,')}
          </span>
          <br />
          <em className="color_gray">
            (
            {g18k_s_ch > 0 ? (
              <span className="red"></span>
            ) : g18k_s_ch < 0 ? (
              <span className="blue"></span>
            ) : (
              <span className="etc">-</span>
            )}{' '}
            {String(g18k_s_ch).replace(/(\d)(?=(?:\d{3})+(?!\d))/g, '$1,')})
          </em>
        </p>
        <p>
          14K{' '}
          <span className="large">
            {String(g14k_s).replace(/(\d)(?=(?:\d{3})+(?!\d))/g, '$1,')}
          </span>
          <br />
          <em className="color_gray">
            (
            {g14k_s_ch > 0 ? (
              <span className="red"></span>
            ) : g14k_s_ch < 0 ? (
              <span className="blue"></span>
            ) : (
              <span className="etc">-</span>
            )}{' '}
            {String(g14k_s_ch).replace(/(\d)(?=(?:\d{3})+(?!\d))/g, '$1,')})
          </em>
        </p>
      </div>
    </li>
  );
};

export default GoldPageSell;
