import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import { Link } from 'react-router-dom';
import ReactSVG from '../../images/react_logo.svg';
import './GoldItem.scss';

@inject('gold')
@observer
class GoldItem extends Component {
  delayPageMove = pageName => {
    const {
      history: { push }
    } = this.props;
    const { common } = this.props.gold.root;
    common.loadingDelay(push, pageName);
  };

  render() {
    const { delayPageMove } = this;
    const { gold } = this.props;

    // const buyPrep = gold.buySise.map((info, index) => {
    //   if (index > 0) {
    //     return false;
    //   } else {
    //     return info.g24k_b_ch === 'up' ? (
    //       <span key={index} className="up">
    //         ( {info.buyPrep})
    //       </span>
    //     ) : info.fluctuation === 'down' ? (
    //       <span key={index} className="down">
    //         ( {info.buyPrep})
    //       </span>
    //     ) : (
    //       <span key={index}>(- {info.buyPrep})</span>
    //     );
    //   }
    // });

    return (
      <li className="gold">
        <div className="box">
          <p className="title"> </p>
          <p className="ts">Gold 24K 3.75g</p>
          <div className="table_box">
            <table>
              <colgroup>
                <col className="col" />
                <col />
              </colgroup>
              <tbody>
                <tr>
                  <th></th>
                  <td>
                    {String(gold.todayBuySise).replace(
                      /(\d)(?=(?:\d{3})+(?!\d))/g,
                      '$1,'
                    )}
                    {' '}
                    <span className="small">
                      ({' '}
                      {gold.todayBuySisePrep > 0 ? (
                        <span className="red"></span>
                      ) : gold.todayBuySisePrep < 0 ? (
                        <span className="blue"></span>
                      ) : (
                        <span className="etc">-</span>
                      )}{' '}
                      {gold.todayBuySisePrep})
                    </span>
                  </td>
                </tr>
                <tr>
                  <th></th>
                  <td>
                    {String(gold.todaySellSise).replace(
                      /(\d)(?=(?:\d{3})+(?!\d))/g,
                      '$1,'
                    )}
                    
                  </td>
                </tr>
              </tbody>
            </table>
          </div>

          <div className="round left">
            <img src={ReactSVG} alt="React Logo" />
          </div>
        </div>

        <Link
          to={{
            pathname: ''
          }}
          onClick={() => delayPageMove('gold')}
        >
          <div className="summary">
            <div className="line">
              <p className="t"> </p>
              <p className="u">ReactApp, </p>
              <p className="s">2019.07.24</p>
            </div>
          </div>
        </Link>
      </li>
    );
  }
}

export default GoldItem;
